<template>
    <div class="item" @click="toStore">
        <img :src="item.pic" alt="">
        <div class="item_right">
            <div class="title">{{ item.title }}</div>
            <div>月售{{ item.sales }}</div>
            <div>起送￥ {{ item.price }} 免配送费</div>
            <div class="label">
                <div v-for="(i, index) in item.label" :key="index">
                    {{ i }}
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import {useRouter} from "vue-router"
export default {
    props: ["item"],
    setup() {
        const router=useRouter()
        const toStore=()=>{
            router.push("/store")  
        }
        return {
            toStore
        }
    }
}
</script>
<style scoped lang="less">
.item {
    background-color: #fff;
    border-radius: 10px;
    display: flex;
    padding: 10px;

    img {
        width: 120px;
        height: 120px;
        border-radius: 10px;
        margin-right: 20px;
    }

    .item_right {
        display: flex;
        flex-flow: column;
        justify-content: space-between;

        .title {
            font-size: 18px;
            font-weight: 600;
        }

        .label {
            display: flex;

            div {
                background-color: #ffc400;
                padding: 2px 5px;
                border-radius: 5px;
                color: #fff;
            }

            div:first-child {
                margin-right: 5px;
            }
        }
    }
}</style>